import ReactDOM from 'react-dom';

import './index.css';
import React from 'react';
import App from './App';
import Project1 from './components/Project1/Project1';
import Project2 from './components/Project2/Project2';
import Project3 from './components/Project3/Project3';
import HomePage from './Pages/HomePage';
import { HashRouter, Route } from 'react-router-dom';


function App2() {
    return (
        <React.Fragment>
            <Route path="/1">
                <Project1></Project1>
            </Route>
            <Route path="/2" component={Project2}></Route>
            <Route path="/3" component={Project3}></Route>
            {/* <Route path="/4" component={Project4}></Route>
            <Route path="/6" component={Tfjs}></Route> */}
            <Route exact path="/" component={HomePage}></Route>
        </React.Fragment>
    )
}

ReactDOM.render(<HashRouter><App2/></HashRouter>, document.getElementById('root'));
